@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Heavy.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto Slab';
  src: url("../fonts/RobotoSlab-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-HeavyItalic.woff2") format("woff2");
  font-weight: 900;
  font-style: italic;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #4b5157;
  background-color: #E5E5E5;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.header {
  background-color: #262D33;
  padding: 15px 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  width: 163px;
  height: 40px;
}

.nav {
  color: #fff;
}

.nav__list {
  display: flex;
}

.nav__link {
  font-family: "Lato";
  font-weight: 900;
  font-size: 14px;
  padding: 25px 15px;
}

.search {
  position: relative;
  width: 263px;
  height: 39px;
  background-color: #656B70;
  border-radius: 10px;
  overflow: hidden;
}

.search__input {
  outline: none;
  padding-left: 23px;
  padding-right: 75px;
  padding: 9px 75px 9px 25px;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}

.search__input::placeholder {
  color: #939699;
  font-size: 14px;
  line-height: 20px;
}

.search__button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 71px;
}

.search__button:hover {
  background-color: #4d5155;
}

.search__button-img {
  width: 17.5px;
  height: 18px;
}

.hero__bg {
  position: absolute;
  background-color: #262D33;
  height: 475px;
  width: 100%;
  margin-top: -30px;
}

.hero__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 570px;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 10px;
  overflow: hidden;
}

.hero__content {
  position: relative;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 55px 55px 100px 55px;
  background-image: url("../img/hero-1.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #939699;
  z-index: 10;
}

.hero__content::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
  z-index: -1;
}

.hero__content--active {
  display: flex;
}

.hero__content-subtitle {
  margin-bottom: 21px;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #4592ff;
}

.hero__content-title {
  max-width: 70%;
  margin-bottom: 49px;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 40px;
  line-height: 50px;
  color: #fff;
}

.hero__sidebar {
  height: 100%;
  max-width: 263px;
  background-color: #fff;
}

.hero__sidebar-title {
  padding: 23px 25px 20px;
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.hero__sidebar-item {
  height: 100px;
  border-top: 1px solid #D9DADB;
}

.hero__sidebar-item:last-child div {
  height: 112%;
}

.hero__sidebar-link {
  padding: 19px 25px 19px;
  display: block;
  height: 100%;
  cursor: pointer;
}

.hero__sidebar-link--active {
  background-color: #F5F5F5;
}

.hero__sidebar-link:hover {
  background-color: #F5F5F5;
}

.hero__sidebar-subtitle {
  margin-bottom: 9px;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.hero__sidebar-subtitle--1 {
  color: #3BBDC4;
}

.hero__sidebar-subtitle--2 {
  color: #6E99AE;
}

.hero__sidebar-subtitle--3 {
  color: #AC8EE3;
}

.hero__sidebar-subtitle--4 {
  color: #FFA34D;
}

.hero__sidebar-subtitle--5 {
  color: #3DC47E;
}

.hero__sidebar-text {
  font-size: 14px;
  line-height: 20px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.footer {
  background-color: #fff;
}

.footer__menu {
  display: flex;
  padding-bottom: 65px;
}

.footer__inner {
  flex-basis: 195px;
}

.footer__inner:last-child {
  flex-basis: 165px;
}

.footer__inner--1 {
  border-top: 5px solid #FF4F52;
}

.footer__inner--2 {
  border-top: 5px solid #AC8EE3;
}

.footer__inner--3 {
  border-top: 5px solid #4592FF;
}

.footer__inner--4 {
  border-top: 5px solid #FFA34D;
}

.footer__inner--5 {
  border-top: 5px solid #3DC47E;
}

.footer__inner--6 {
  border-top: 5px solid #6E99AE;
}

.footer__title {
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
  margin-bottom: 27px;
  margin-top: 33px;
}

.footer__logo-link {
  width: 50px;
  height: 50px;
  margin-right: 30px;
}

.footer__item:not(:last-child) {
  margin-bottom: 17px;
}

.footer__link {
  font-size: 14px;
  color: #4b5157;
}

.footer__social {
  display: flex;
  align-items: center;
  padding: 30px 0 29px;
  border-top: 1px solid #D9DADB;
  border-bottom: 1px solid #D9DADB;
}

.footer__social-menu {
  display: flex;
}

.footer__social-item:not(:last-child) {
  margin-right: 30px;
}

.footer__social-link {
  font-weight: 900;
  font-size: 14px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #262D33;
}

.footer__media {
  margin-left: auto;
}

.footer__copy {
  display: flex;
  justify-content: space-between;
  padding-top: 41px;
  padding-bottom: 55px;
}

.footer__copy-descr {
  font-size: 14px;
  line-height: 20px;
  color: #939699;
  width: 70%;
}

.footer__copy-text {
  font-size: 14px;
  line-height: 20px;
  color: #939699;
}

.user {
  display: flex;
  align-items: center;
}

.user__img {
  width: 45px;
  height: 45px;
  margin-right: 10px;
  border-radius: 50%;
}

.user__info {
  display: flex;
  flex-direction: column;
}

.user__name {
  margin-bottom: 5px;
  font-size: 14px;
  color: #fff;
  mix-blend-mode: normal;
  opacity: 0.85;
}

.user__job {
  font-size: 12px;
  color: #fff;
  opacity: 0.4;
}

.button {
  padding: 10px 40px 9px 20px;
  min-width: 154px;
  height: 40px;
  font-weight: 900;
  font-size: 14px;
  color: #fff;
  background-color: #4592ff;
  border-radius: 20px;
}

.button__arrow {
  position: relative;
  text-align: left;
}

.button__arrow::after {
  content: "";
  position: absolute;
  right: 22px;
  top: 50%;
  transform: translatey(-50%);
  width: 15px;
  height: 7px;
  background-image: url("../img/btn-arrow.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.article__list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  background-color: #30363D;
  border-radius: 10px;
  overflow: hidden;
}

.article__item {
  position: relative;
}

.article__item::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 1px;
  height: 80px;
  background-color: #4b5157;
}

.article__item:last-child::after {
  display: none;
}

.article__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 30px 25px 35px;
}

.article__link:hover {
  background-color: #3b434b;
}

.article__title {
  font-weight: 900;
  font-size: 14px;
  max-width: 154px;
  line-height: 20px;
  color: #fff;
  margin-right: 15px;
}

.article__img {
  width: 65px;
  height: 65px;
  object-fit: cover;
}

.grid__list {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 170px;
}

.grid__item {
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
}

.grid__item--big {
  grid-column: span 2;
  grid-row: span 2;
  background-image: url("../img/grid-big.png");
  background-repeat: no-repeat;
  background-position: right 15px;
}

.grid__item--mid {
  grid-row: span 2;
}

.grid__item--social {
  padding: 30px 36px 40px 37px;
  text-align: center;
}

.grid__item--small {
  position: relative;
  padding: 25px;
  background-color: #3BBDC4;
  background-image: url("../img/file.svg");
  background-repeat: no-repeat;
  background-position: right 22px top 24px;
}

.grid__info {
  max-width: 237px;
}

.grid__subtitle {
  margin-bottom: 23px;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #6E99AE;
}

.grid__subtitle--mid {
  font-size: 14px;
  color: #262D33;
  margin-bottom: 0;
  border-bottom: 1px solid #D9DADB;
  padding: 23px 26px 20px;
}

.grid__subtitle--purple {
  color: #E7D9FF;
}

.grid__title {
  font-family: 'Roboto Slab', sans-serif;
  font-size: 25px;
  line-height: 30px;
  color: #262D33;
}

.grid__title--small {
  margin-bottom: 25px;
  font-size: 20px;
  line-height: 25px;
}

.grid__title--mid {
  align-self: flex-end;
  font-size: 16px;
  line-height: 25px;
}

.grid__title--big {
  margin-bottom: 25px;
}

.grid__title--check {
  margin-bottom: 11px;
  font-size: 16px;
  line-height: 25px;
  color: #fff;
}

.grid__title--full {
  font-size: 16px;
  line-height: 25px;
  color: #fff;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  margin-bottom: 27px;
}

.grid__download {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 13px;
  padding-bottom: 15px;
  text-align: center;
  font-weight: 900;
  font-size: 14px;
  color: #fff;
  border-top: 1px solid #96E2E3;
}

.grid__download-img {
  width: 17px;
  height: 17px;
  margin-right: 11px;
}

.grid__file {
  font-size: 14px;
  color: #C1F4F5;
}

.grid__text {
  font-size: 14px;
  line-height: 20px;
}

.grid__tags {
  display: flex;
  flex-wrap: wrap;
  padding: 25px;
  padding-right: 20px;
}

.grid__tag {
  margin-bottom: 13px;
  margin-right: 9px;
}

.grid__tag-link {
  display: inline-block;
  padding: 7px 15px;
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
  border: 1px solid #D9DADB;
  border-radius: 5px;
}

.grid__link {
  position: relative;
  display: block;
  height: 100%;
  width: 100%;
}

.grid__link--big {
  padding: 35px;
  padding-bottom: 90px;
}

.grid__link--mid {
  display: flex;
  background-image: url("../img/grid-mid.jpg");
  background-repeat: no-repeat;
  background-position: top 95px;
  padding: 30px 52px 30px 25px;
}

.grid__link--full {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  padding: 25px;
  background-image: url("../img/grid-full.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.grid__link--full::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
  z-index: -1;
}

.grid__user {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  padding: 12px 35px 13px;
  border-top: 1px solid #D9DADB;
}

.grid__user-info {
  display: flex;
  margin-right: 16px;
}

.grid__user-img {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
}

.grid__user-name {
  margin-right: 5px;
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
}

.grid__user-text {
  font-size: 14px;
}

.tagname {
  position: absolute;
  left: 24px;
  top: 19px;
  padding: 6px 12px;
  display: inline-block;
  font-size: 12px;
  color: #fff;
  background-color: #3DC47E;
  border-radius: 3px;
}

.comment {
  display: flex;
  align-items: center;
}

.comment--mr {
  margin-right: 8px;
}

.comment__img {
  width: 13.5px;
  height: 13.5px;
  margin-right: 6px;
}

.comment__counter {
  font-size: 12px;
  color: #939699;
}

.comment__counter--light {
  color: #fff;
}

.like {
  display: flex;
  align-items: center;
}

.like__img {
  width: 13.5px;
  height: 13.5px;
  margin-right: 6px;
}

.like__counter {
  font-size: 12px;
  color: #fff;
}

.user__descr {
  color: #fff;
  opacity: 0.4;
  display: flex;
}

.user__date {
  font-size: 12px;
  margin-right: 8px;
}

.card__link {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 25px;
  padding-top: 29px;
  padding-bottom: 30px;
}

.card__title {
  margin-bottom: 9px;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 16px;
  color: #262D33;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.card__text {
  margin-bottom: 7px;
  font-size: 14px;
  line-height: 20px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.card__date {
  font-size: 12px;
  color: #939699;
  margin-top: auto;
}

.social {
  display: flex;
  justify-content: space-between;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.social__link--facebook {
  background-color: #4267B2;
}

.social__link--twitter {
  background-color: #1DA1F2;
}

.social__link--youtube {
  background-color: #FF0000;
}

.social__link--instagram {
  background-color: #262626;
}

.social__link--footer {
  width: 40px;
  height: 40px;
}

.social__link--footer:not(:last-child) {
  margin-right: 15px;
}

.social__img {
  width: 20px;
  height: 20px;
}

.banner {
  position: relative;
  margin: 60px 0;
  padding-top: 51px;
  padding-bottom: 54px;
  background-image: url("../img/banner.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  text-align: center;
  z-index: 10;
}

.banner::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
  z-index: -1;
}

.banner__title {
  max-width: 80%;
  margin-bottom: 25px;
  margin-right: auto;
  margin-left: auto;
  font-family: "Roboto Slab", sans-serif;
  font-size: 40px;
  line-height: 50px;
  color: #fff;
}

.banner__button {
  min-width: 161px;
}

.feed {
  margin-bottom: 60px;
}

.feed__container {
  display: flex;
}

.feed__list {
  margin-right: 29px;
  background-color: #fff;
  border-radius: 10px;
}

.feed__item {
  position: relative;
  display: flex;
  padding: 25px;
}

.feed__item:not(:last-child)::after {
  content: '';
  position: absolute;
  bottom: 0;
  width: calc(100% - 50px);
  height: 1px;
  background-color: #D9DADB;
}

.feed__img {
  margin-right: 25px;
  width: 336px;
  height: 195px;
  object-fit: cover;
}

.feed__content {
  display: flex;
  flex-direction: column;
}

.feed__subtitle {
  margin-top: 5px;
  margin-bottom: 15px;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.feed__subtitle--1 {
  color: #4592FF;
}

.feed__subtitle--2 {
  color: #3BBDC4;
}

.feed__subtitle--3 {
  color: #FF4F52;
}

.feed__subtitle--4 {
  color: #3BBDC4;
}

.feed__subtitle--5 {
  color: #FF4F52;
}

.feed__subtitle--6 {
  color: #AC8EE3;
}

.feed__title {
  margin-bottom: 11px;
  font-family: "Roboto Slab", sans-serif;
  font-size: 20px;
  line-height: 25px;
  color: #262D33;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.feed__text {
  font-size: 14px;
  line-height: 20px;
  color: #4b5157;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.feed__icon {
  position: absolute;
  top: 26px;
  right: 28px;
  width: 14px;
  height: 18px;
}

.feed__bottom {
  margin-top: auto;
  margin-bottom: 5px;
  display: flex;
}

.feed__date {
  margin-right: 13px;
  color: #939699;
  font-size: 12px;
}

.comment__img--grey {
  fill: #BCBFC2;
}

.comment__counter--grey {
  color: #939699;
}

.like__img--grey {
  fill: #BCBFC2;
}

.like__counter--grey {
  color: #939699;
}

.sidebar {
  min-width: 263px;
  height: 100%;
  background-color: #fff;
  border-radius: 10px;
}

.sidebar__subtitle {
  font-weight: 900;
  font-size: 14px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #262D33;
  padding: 23px 20px 20px;
  text-align: center;
  border-bottom: 1px solid #D9DADB;
}

.sidebar__item {
  display: flex;
  justify-content: center;
  position: relative;
}

.sidebar__item:not(:last-child)::after {
  content: '';
  position: absolute;
  bottom: 0;
  width: calc(100% - 50px);
  height: 1px;
  background-color: #D9DADB;
}

.sidebar__link {
  display: flex;
  padding: 18px 25px 17px;
  justify-content: space-between;
}

.sidebar__img {
  width: 60px;
  height: 60px;
  margin-right: 10px;
}

.sidebar__title {
  font-size: 14px;
  line-height: 20px;
  color: #4b5157;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.sidebar__time {
  font-size: 12px;
  color: #939699;
}

.sidebar__bottom-link {
  display: block;
  font-weight: 900;
  font-size: 14px;
  color: #262D33;
  padding: 12px 20px 16px;
  text-align: center;
  border-top: 1px solid #D9DADB;
}

.news {
  padding: 60px 0;
  background-color: #262D33;
}

.news__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 170px;
  gap: 30px;
}

.news__slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  grid-column: span 2;
  grid-row: span 3;
  border-radius: 10px;
}

.news__item {
  border-radius: 10px;
  background-color: #fff;
}

.news__item--big {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url("../img/news-bg.jpg");
  padding-top: 50px;
  padding-bottom: 40px;
  z-index: 10;
}

.news__item--big::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(64, 48, 61, 0.35), rgba(64, 48, 61, 0.35));
  z-index: -1;
}

.news__item--mid {
  display: flex;
  position: relative;
  grid-column: span 2;
  grid-row: span 2;
  background-color: #B4D1E0;
  background-image: url("../img/circle.svg");
  background-repeat: no-repeat;
  background-position: right 30px top 65px;
  z-index: 1;
  overflow: hidden;
}

.news__content {
  padding-top: 35px;
  padding-right: 35px;
  padding-left: 35px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: start;
}

.news__img--mid {
  width: 270px;
  height: 370px;
}

.news__subtitle {
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #6E99AE;
  margin-bottom: 23px;
}

.news__title {
  margin-bottom: 43px;
  font-family: "Roboto Slab", sans-serif;
  font-size: 40px;
  line-height: 50px;
  text-align: center;
  color: #fff;
}

.news__title--mid {
  font-size: 25px;
  line-height: 30px;
  color: #262D33;
  text-align: left;
  margin-bottom: 15px;
}

.news__user {
  align-items: normal;
  flex-direction: column;
  align-items: center;
  margin-bottom: 33px;
}

.news__tagname {
  left: 19px;
  top: 19px;
}

.news__text {
  font-size: 14px;
  line-height: 20px;
  font-size: #4b5157;
}

.news__btn {
  margin-top: auto;
}

.news__button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 194px;
  padding: 10px 20px;
  box-sizing: border-box;
}

.news__button-img {
  width: 19px;
  height: 15px;
  margin-right: 6px;
}

.news__button span {
  opacity: 0.4;
  margin-left: 10px;
}

.news__pagination {
  margin-bottom: 30px;
}

.news__bullet {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin: 0 8px;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.4;
  cursor: pointer;
}

.news__bullet--active {
  opacity: 1;
}

.user__img--news {
  margin-right: 0;
  margin-bottom: 10px;
}

.user__job--news {
  text-align: center;
}

.subscribe {
  padding-top: 64px;
  padding-bottom: 70px;
  background-color: #fff;
}

.subscribe__title {
  margin-bottom: 25px;
  font-family: "Roboto Slab", sans-serif;
  font-size: 40px;
  color: #262D33;
  text-align: center;
}

.form {
  display: flex;
  justify-content: center;
}

.form__content {
  display: flex;
}

.form__input {
  width: 313px;
  height: 40px;
  margin-right: 10px;
  padding: 5px 14px;
  border: 1px solid #D9DADB;
  border-radius: 5px;
}

.form__input::placeholder {
  font-size: 14px;
  color: #939699;
}

.form__button {
  min-width: 136px;
  padding: 10px 25px;
}
